import { shallowEqual } from "react-redux";
import { getPersonWages } from "store/thunks/finance";
import { MPerson } from "store/types/models/id";
import useLoadAndSelect from "../useLoadAndSelect";

export function usePersonWages(personId: MPerson['_id']) {
    return useLoadAndSelect({
        actionCreator: personId ? getPersonWages : undefined,
        selector: state => Object.values(state.finance.wages).filter( wage => wage.personId === personId),
        equality: shallowEqual,
        postParams: personId,
        deduplicate: ['personWages', personId]
    })
}

export default usePersonWages