////// Packages //////////////////
import React, { useCallback, FC, useState } from 'react';
import { Row, Col, Button, Modal, CustomInput, Label, FormGroup, ModalHeader, ModalBody, ModalFooter, Form } from 'reactstrap';
import dayjs from 'dayjs';
import BootstrapTable, { ColumnDescription } from 'react-bootstrap-table-next';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { toast } from 'react-toastify';
import { useSelector, useDispatch } from 'react-redux';
import confirm from 'reactstrap-confirm';

////// Local Components //////////
import DetailRow from './DetailRow';
import { currencyFormat } from 'environ';

////// Global Components /////////
import FormGroupSelect from 'components/FormGroupSelect';
import FormGroupInput from 'components/FormGroupInput';
import CurrencyInput from 'components/CurrencyInput';
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup';
import Flex from 'components/Flex';

////// CommonJS Helpers //////////
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////
import useUser from 'store/hooks/user/useUser';
import useAction from 'store/hooks/useAction';
import usePersonWages from 'store/hooks/finance/usePersonWages';

////// Thunks ////////////////////
import { wages as wageController } from 'store/thunks/finance';

////// Types /////////////////////
import { MWage } from 'store/types/models/finance';



interface Props {
    userId: string;
    editMode: boolean;
    setEditMode: (editMode: boolean) => void;
}

const wageTypeOptions = ['Salary', 'Hourly', 'Neither']
const salaryPeriodOptions = ['Year', 'Month', 'Week']

const UserWages: FC<Props> = ({ userId, editMode, setEditMode }) => {
    const { value: user } = useUser(userId);
    const person = useSelector(state => user && state.id.people[user.personId]);
    const { value: wages } = usePersonWages( user?.personId || '')
    const dispatch = useDispatch()
    const createWageAction = useAction(wageController.createWage);
    const deleteWageAction = useAction(wageController.deleteWage);

    // History table configuration
    const dateFormatter = useCallback(date => date ? dayjs(date).format('MM/DD/YYYY') : 'unknown', [])
    // const wages = [
    //     { id: '598yd089y09uu0t9024r', effectiveDate: '11-27-2017', wageType: 'Hourly', wageRate: 32, hourlyRate: 32, schedule: 'weekly', scheduledHours: 40, weekdays: [1, 2, 3, 4, 5] },
    //     { id: 2, effectiveDate: '12-17-2017', wageType: 'Annual', wageRate: 76000, hourlyRate: 37.23, schedule: 'weekly', scheduledHours: 32, weekdays: 4 },
    //     { id: 3, effectiveDate: '1-1-2017', wageType: 'Annual', wageRate: 82000, hourlyRate: 40, schedule: 'weekly', scheduledHours: 40, weekdays: [0, 3, 5, 7] }
    // ]


    const [wageRate, setWageRate] = useState<number>()
    const [wageType, setWageType] = useState<string>();
    const [salaryPeriod, setSalaryPeriod] = useState<string>();
    const [hourlyEquivalent, setHourlyEquivalent] = useState<number>();
    const [showCalculator, setShowCalculator] = useState<boolean>(false);
    const [effectiveDate, setEffectiveDate] = useState<string>();
    const [isFullTime, setIsFullTime] = useState(false);
    const [overtime, setOvertime] = useState(false);
    const [doublePayOvertime, setDoublePayOvertime] = useState(false);
    const [weeksPerYear, setWeeksPerYear] = useState<number>();
    const [hoursPerPeriod, setHoursPerPeriod] = useState<number>();

    const currentWages = wages?.reduce((current, wage) => current && !dayjs(wage.effectiveDate).isAfter(current.effectiveDate) ? current : wage, wages[0] )

    const columns: ColumnDescription[] = [
        {
            dataField: 'effectiveDate',
            text: 'Effective Date',
            formatter: dateFormatter,
            sort: true
        },
        {
            dataField: 'wageType',
            text: 'Wage Type',
            formatter: (wt, row: MWage) => wt + (wt === 'Salary' ? ' - ' + row.salaryPeriod : '')
        },
        {
            dataField: 'wageAmount',
            text: 'Wage Rate',
            formatter: (rate: unknown) => (typeof rate === 'number' && !isNaN(rate)) ? currencyFormat.format(rate) : 'unknown'
        },
        {
            dataField: 'overtimeEligible',
            text: 'Overtime',
            formatter: (ot?: boolean) => <FontAwesomeIcon icon={ot ? 'check' : 'times'} className={ ot ? "text-primary" : "text-secondary" } />  // eslint-disable-line
        },
        {
            dataField: 'doubleTimeEligible',
            text: 'Double Time',
            formatter: (ot?: boolean) => <FontAwesomeIcon icon={ot ? 'check' : 'times'} className={ ot ? "text-primary" : "text-secondary" } /> // eslint-disable-line
        }
    ]

    async function handleDeleteWage(_id: MWage["_id"]) {
        if (!_id) return
        const confirmResponse = await confirm({
            message: 'Are you sure you want to delete this wage entry?',
            confirmText: 'Delete',
            confirmColor: 'danger',
            cancelText: 'Cancel',
            cancelColor: 'secondary'
        })
        if (!confirmResponse) return
        const data = await deleteWageAction.post(_id)
        if (data?.success) {
            toast.success('Deleted wage')
            dispatch( wageController.remove(_id) )
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }
    if (editMode) {
        columns.push({
            dataField: '_id',
            text: '',
            formatter: (_id: MWage['_id']) => <Button size="sm" color="falcon-danger" onClick={()=>handleDeleteWage(_id)}><FontAwesomeIcon icon="times" /></Button> // eslint-disable-line
        })
    }

    const calcHourlyRate = salaryPeriod === 'Year'
        ? wageRate && weeksPerYear && hoursPerPeriod && wageRate / weeksPerYear / hoursPerPeriod
        : (salaryPeriod === 'Month' || salaryPeriod === 'Week')
            ? wageRate && hoursPerPeriod && wageRate / hoursPerPeriod
            : wageRate

    function clearForm() {
        setWageRate(undefined)
        setWageType(undefined)
        setSalaryPeriod(undefined)
        setHourlyEquivalent(undefined)
        setEffectiveDate(undefined)
        setIsFullTime(false)
        setOvertime(false)
        setDoublePayOvertime(false)
        setWeeksPerYear(undefined)
        setHoursPerPeriod(undefined)
    }
    
    async function handleSubmitWage() {
        if (
            person?._id
            && effectiveDate
            && wageRate
            && wageType
            && (wageType !== 'Salary' || salaryPeriod)
            && (wageType === 'Hourly' || hourlyEquivalent)
        ) {
            const data = await createWageAction.post({
                personId: person._id,
                effectiveDate,
                wageAmount: wageRate,
                wageType,
                salaryPeriod,
                hourlyRate: wageType === 'Hourly' ? wageRate : hourlyEquivalent,
                overtimeEligible: overtime,
                doubleTimeEligible: doublePayOvertime
            })
            if (data?.success) {
                toast.success( 'Added wage' )
                clearForm()
            } else {
                toast.error(data?.error || 'Something went wrong')
            }
        } else {
            toast.error('All required fields not filled')
        }
        
        
    }
    return (
        <Row>
            <Col xs={12} lg={7} xl={5} className="border-lg-right">
                <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">Current Wages</h6>
                {
                    editMode
                        ?
                        <>
                            <Row>
                                <Col>
                                    <FormGroupSelect
                                        id='wage-type'
                                        label='Wage Type'
                                        placeholder='Select wage type'
                                        classNamePrefix="react-select"
                                        options={wageTypeOptions?.map(option => ({ value: option, label: option }))}
                                        value={wageType && { value: wageType, label: wageType }}
                                        onChange={option => setWageType(option.value)}
                                    />
                                </Col>
                            </Row>
                            <Form inline className="my-2">
                                <CurrencyInput
                                    id='wage-rate'
                                    label='Wage Rate'
                                    labelProps={{
                                        className: "mr-2"
                                    }}
                                    value={wageRate}
                                    onChange={e => {
                                        if (e.target.value && !isNaN(+e.target.value)) {
                                            setWageRate(+e.target.value)
                                        }
                                    }}
                                    placeholder='Rate ($)'
                                    name='wage-rate'
                                />
                                {
                                    wageType === 'Salary' &&
                                    <FormGroupSelect
                                        id='salary-period'
                                        label='per'
                                        labelProps={{
                                            className: "mx-2"
                                        }}
                                        styles={{
                                            control: provided => ({
                                                ...provided,
                                                width: 200
                                            })
                                        }}
                                        placeholder='Salary Period'
                                        classNamePrefix="react-select"
                                        options={salaryPeriodOptions?.map(option => ({ value: option, label: option }))}
                                        value={salaryPeriod && { value: salaryPeriod, label: salaryPeriod }}
                                        onChange={option => setSalaryPeriod(option.value)}
                                    />
                                }
                            </Form>
                            {wageType === 'Salary' &&
                                <Form inline className="py-2">
                                    <CurrencyInput
                                        id='hourly-equivalent'
                                        label='Hourly Equivalent'
                                        value={hourlyEquivalent}
                                        onChange={e => {
                                            if (e.target.value && !isNaN(+e.target.value)) {
                                                setHourlyEquivalent(+e.target.value)
                                            }
                                        }}
                                        placeholder='Hourly equivalent'
                                        name='hourly-equivalent'
                                        labelProps={{
                                            className: "mr-2"
                                        }}
                                    />
                                    {
                                        wageRate && salaryPeriod &&
                                        <>
                                            <Button onClick={() => setShowCalculator(true)} color='primary' className='ml-2'>
                                                <FontAwesomeIcon icon='calculator' className='mr-1' />
                                                Calculate
                                            </Button>
                                            <Modal size="md" isOpen={showCalculator} toggle={() => setShowCalculator(false)}>
                                                <ModalHeader className='bg-light border-bottom-0'>
                                                    Hourly Equivalent Calculator
                                                </ModalHeader>
                                                <ModalBody>
                                                    <CustomInput
                                                        id="full-time"
                                                        type="switch"
                                                        label="Full time"
                                                        checked={isFullTime}
                                                        className="mt-2 mb-4"
                                                        onChange={e => {
                                                            setIsFullTime(e.target.checked);
                                                            if (e.target.checked) {
                                                                setWeeksPerYear(52);
                                                                setHoursPerPeriod(salaryPeriod === "Month" ? 173.3 : 40);
                                                            }
                                                        }}
                                                    />
                                                    {
                                                        salaryPeriod === 'Year' &&
                                                        <FormGroupInput
                                                            id="weeks-per-year"
                                                            type="number"
                                                            label="Weeks per Year"
                                                            min={1}
                                                            max={52}
                                                            value={weeksPerYear}
                                                            onChange={e => {
                                                                setIsFullTime(false)
                                                                setWeeksPerYear(e.target.value ? +e.target.value : undefined)
                                                            }}
                                                        />
                                                    }
                                                    <FormGroupInput
                                                        id="hours-per-period"
                                                        type="number"
                                                        label={"Hours per " + (salaryPeriod === 'Month' ? 'Month' : 'Week')}
                                                        value={hoursPerPeriod}
                                                        onChange={e => {
                                                            setIsFullTime(false)
                                                            setHoursPerPeriod(e.target.value ? +e.target.value : undefined)
                                                        }}
                                                    />
                                                    <h5 className="text-right mt-4">{'Hourly Rate: ' + (calcHourlyRate && currencyFormat.format(calcHourlyRate))}</h5>

                                                </ModalBody>
                                                <ModalFooter className='bg-light border-top-0'>
                                                    <Button color="secondary" size='sm' onClick={() => setShowCalculator(false)} className="mr-2">
                                                        Cancel
                                                    </Button>
                                                    <Button color="primary" size='sm' disabled={!calcHourlyRate} onClick={() => {
                                                        const target = document.getElementById('hourly-equivalent') as HTMLInputElement | null
                                                        if (target) {
                                                            const nativeInputValueSetter = Object.getOwnPropertyDescriptor(window.HTMLInputElement.prototype, "value")!.set!;
                                                            nativeInputValueSetter.call(target, calcHourlyRate);
                                                            const changeEvent = new Event('input', { bubbles: true});
                                                            target.dispatchEvent(changeEvent);
                                                        }
                                                        setHourlyEquivalent(calcHourlyRate)
                                                        setShowCalculator(false)
                                                    }}>
                                                        Apply
                                                    </Button>
                                                </ModalFooter>
                                            </Modal>
                                        </>
                                    }
                                </Form>
                            }
                            <Row>
                                <Col>
                                    <FormGroup>
                                        <Label>
                                            Effective Date
                                                </Label>
                                        <DateTimeInputGroup
                                            id="wage-effective-date"
                                            value={effectiveDate}
                                            onChange={date => setEffectiveDate(date)}
                                            timeFormat={false}
                                            dateFormat="MM/DD/YYYY"
                                            pickerProps={{
                                                closeOnSelect: true
                                            }}
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <h6>
                                        {makeName(person?.name, { first: true, common: true })}
                                        {` receives:`}
                                    </h6>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <CustomInput
                                        id="overtime"
                                        type="checkbox"
                                        label="Overtime"
                                        checked={overtime}
                                        onChange={e => setOvertime(e.target.checked)}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <CustomInput
                                        id="double-pay-overtime"
                                        type="checkbox"
                                        label="Double Pay Overtime"
                                        checked={doublePayOvertime}
                                        onChange={e => setDoublePayOvertime(e.target.checked)}
                                    />
                                </Col>
                            </Row>
                            <Flex justify="end">
                                <Button color="primary" onClick={handleSubmitWage}>
                                    Submit
                                </Button>
                            </Flex>
                        </>

                        //edit interface

                        :
                        // display interface
                        <>
                            <DetailRow title="Type">{currentWages?.wageType}</DetailRow>
                            <DetailRow title="Rate">
                                {
                                    currentWages?.wageAmount && 
                                    (
                                        currencyFormat.format(currentWages.wageAmount) +
                                        (currentWages.wageType === 'Salary' ? ' per ' + currentWages.salaryPeriod : '')
                                    )
                                }
                            </DetailRow>
                            {currentWages?.wageType !== "Hourly" &&
                                <DetailRow title="Hourly Equivalent">
                                    {currentWages?.hourlyRate && currencyFormat.format(currentWages.hourlyRate)}
                                </DetailRow>
                            }
                        </>

                }


            </Col>
            <Col xs={12} lg={5} xl={7} className="mt-4 mt-lg-0">
                <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">Wage History</h6>
                <BootstrapTable
                    keyField='id'
                    bootstrap4
                    data={wages || []}
                    columns={columns}
                    bordered={false}
                    classes="table-dashboard table-striped table-sm fs-0 border-bottom border-200 mb-0 table-dashboard-th-nowrap" 
                    rowClasses="btn-reveal-trigger border-top border-200"
                    headerClasses="bg-200 text-900 border-y border-200 "
                />
            </Col>
        </Row>
    )

}
export default UserWages;