import  { useSelector } from 'react-redux';
import { intersection, isEqual } from 'lodash';


import permissions from 'store/permissions';
import { organizations, getPrivateOrgInfo} from "store/thunks/organizations";
import { MOrganization } from 'store/types/models/id';
import useLoadAndSelect from 'store/hooks/useLoadAndSelect';
import useMyPermissions from 'store/hooks/user/useMyPermissions';

const adminPermissions = [
    permissions.GLOBAL_ADMIN,
    permissions.EDIT_ORGANIZATION
];

function useMyOrganization () {
    const myOrgId = useSelector(state => state.app.login.organizationId!);

    const myPermissions = useMyPermissions();

    const isAdmin = intersection(adminPermissions, myPermissions).length > 0;
    
    const myOrg= useLoadAndSelect<MOrganization>({
        actionCreator: isAdmin ? organizations.getAdminInfo : getPrivateOrgInfo,
        selector: state => state.id.organizations[myOrgId || ''],
        equality: isEqual,
        postParams: myOrgId,
        deduplicate: ['organization', myOrgId, isAdmin ? 'admin' : 'private']
    })

    return myOrg;
}

export default useMyOrganization;