////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import FalconCardHeader from 'components/FalconCardHeader'
import Flex from 'components/Flex'
import React, { FC, useState } from 'react'
import { toast } from 'react-toastify'
import { Button, Card, CardBody, Input, InputGroup } from 'reactstrap'
import useAction from 'store/hooks/useAction'
import useStageOptions from 'store/hooks/workflow/useStageOptions'
import { stages } from 'store/thunks/workflow/stages'

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props {
    className?: string;
}

const WorkflowSettings: FC<Props> = ({className}) => {
    const {value: stageTypes, ...stageTypesLoader} = useStageOptions()
    const createTypeAction = useAction( stages.createNewType );

    const [newType, setNewType] = useState<string>();

    async function handleAdd() {
        const data = await createTypeAction.post(newType)
        if (data?.success) {
            toast.success('Created new stage type ' + newType)
            setNewType('')
        } else {
            toast.error(data?.error || "Something went wrong")
        }
    }

    async function handleDelete(type: string) {
        // controller not yet built
    }

    return (
        <Card className={className}>
            <FalconCardHeader
                title="Workflow Settings"
            />
            <CardBody>
                <Card>
                    <CardBody>
                        <h5 className="mb-2">Stage Type Options</h5>
                        <hr />
                        <InputGroup>
                            <Input
                                placeholder="New type..."
                                value={newType}
                                onChange={e=>setNewType(e.target.value)}
                            />
                            <Button color="outline-success" onClick={handleAdd}>
                                <FontAwesomeIcon icon="check" />
                            </Button>
                        </InputGroup>
                        <Flex justify="center">
                            {stageTypes?.map( type => (
                                <Card className="border d-inline-block mt-3 mx-2" key={type}>
                                    <CardBody tag={Flex} align="center" justify="between">
                                        <h6 className="mr-4">{type}</h6>
                                        <Button color="falcon-danger" onClick={()=>handleDelete(type)}>
                                            <FontAwesomeIcon icon="trash-alt" />
                                        </Button>
                                    </CardBody>
                                </Card>
                            ))}
                        </Flex>
                        
                    </CardBody>
                </Card>
            </CardBody>
        </Card>
    )
}

export default WorkflowSettings