import { getStageTypes } from 'store/thunks/settings';
import useLoadAndSelect from 'store/hooks/useLoadAndSelect';

function useStageOptions() {
    const stageOptions = useLoadAndSelect({
        actionCreator: getStageTypes,
        selector: state => state.app.options?.stage.types, 
        deduplicate: ['stageOptions']
    })

    return stageOptions;
}

export default useStageOptions;