////// Packages //////////////////
import React, { useEffect, useState, FC } from 'react';
import {Button, Col, Row, Alert} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import ModalMediaContent from 'compositions/modal/ModalMediaContent';

////// Global Components /////////
import EditableSection from 'compositions/modal/EditableSection';
import FormGroupSelect from 'components/FormGroupSelect';
import PhoneNumberInput from 'components/PhoneNumberInput';


////// CommonJS Helpers //////////
import {phoneOptions} from 'helpers/sysConstants';
import {IPhone} from 'store/types/models/id';

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendOrgPhones } from 'store/thunks/organizations';

interface Props {
    phones?: IPhone[];
    organizationId: string;
}

const ModalPhones: FC<Props> = ({phones, organizationId}) => {
    const setPhones = useAction(sendOrgPhones)

    /* phones: [{
        description: String,
        number: String
    }] */
    const [newPhones, setNewPhones] = useState(phones || []);

    const handlePhoneRemove = (index: number) => () => setNewPhones( newPhones.filter( (v, i) => i !== index) )   

    const handleDescriptionChange = (index: number) => (obj: any) => setNewPhones( newPhones.map( (v, i) => i === index ? {description: obj.value, number: v.number} : v ))

    const handleNumberChange = (index: number) => (e: React.ChangeEvent<HTMLInputElement>) => setNewPhones(newPhones.map( (v, i) => i === index ? {description: v.description || 'home', number: e.target.value} : v));

    const handlePhoneAdd = () => setNewPhones( [...newPhones, {description: '', number: ''}] )

    useEffect(() => {
        setNewPhones(phones || []);
    }, [phones]);

    // useEffect(() => {
    //     setError( newPhones.map( (v, i) => v?.number?.length === pattern.length ? null : 'Invalid phone number input') );
    // }, [newPhones]);
    
    const handleSubmit = async () => {
        const data = await setPhones.post(newPhones, organizationId);
        return data?.success;
    }

    return (
        <ModalMediaContent title="Phone Numbers" icon="phone">
            <EditableSection
                baseContent={
                    phones?.map(({ _id, description, number }) => (
                        <div key={_id}>
                            <strong><em>{description}: </em></strong> {number}
                        </div>
                    ))
                }
                editContent={
                    <>
                        {newPhones?.map(({ description, number }, index) => (
                            <Row key={index} className="no-gutters btn-reveal-trigger">
                                <Col xs="auto">
                                    <Button color="link" className="btn-reveal py-0 px-2" onClick={handlePhoneRemove(index)}>
                                        <FontAwesomeIcon icon="trash" />
                                    </Button>
                                </Col>
                                <Col xs lg="4">
                                    <FormGroupSelect
                                        id={`phone-description-${index}`}
                                        options={phoneOptions}
                                        label=''
                                        value={description}
                                        onChange={handleDescriptionChange(index)}
                                        className="mr-1"
                                        placeholder="Select Type.."
                                    />
                                </Col>
                                <Col lg >
                                    <PhoneNumberInput 
                                        id={`phone-number-${index}`}
                                        label=''
                                        value={number}
                                        onChange={handleNumberChange(index)}
                                    />
                                </Col>
                            </Row>
                        ))}
                        <Row>
                            <Col xs="auto">
                                <Button color="link" className="btn-reveal py-0 px-2" onClick={handlePhoneAdd}>
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                            </Col>
                        </Row>
                        {setPhones.error && 
                            <Alert color="danger">{setPhones.error}</Alert>
                        }
                    </>

                }
                handleSubmit={handleSubmit}
                loading={setPhones.loading}
                />
        </ModalMediaContent>
    )
    
};


export default ModalPhones;
