////// Packages //////////////////
import React, { useState, FC } from 'react';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

///// Types //////////////////////
import { InputProps } from 'reactstrap'


const withValueMask = (OriginalComponent: React.FC<InputProps> | React.ComponentType<InputProps>, formatter: (input: string)=>string) => {


  const UpdatedComponent: FC<InputProps> = ({value, onChange, ...props}) => {
    const [focus, setFocus] = useState(false);
    const [storedVal, setStoredVal] = useState(value?.toString() || '');

    const formattedVal =  formatter(storedVal)
    
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setStoredVal(event.target.value);
      if (onChange) onChange(event);
    }

    
    return (
      <OriginalComponent
        value={focus ? storedVal : formattedVal} 
        onChange={handleChange} 
        onFocus={()=>setFocus(true)}
        onBlur={()=>setFocus(false)}
        {...props} 
      />
    )
  };

  return UpdatedComponent;
};

export default withValueMask;