////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import PersonMediaSmall from 'compositions/PersonMediaSmall'
import React, { FC } from 'react'
import { useSelector } from 'react-redux'
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap'
import useMyOrganization from 'store/hooks/organization/useMyOrganization'
import TimesheetSettings from './TimesheetSettings'
import WorkflowSettings from './WorkflowSettings'

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props {
    
}

const Settings: FC<Props> = () => {
    const { value: organization, ...organizationLoader } = useMyOrganization()
    
    return (
        <>
            <Card>
                <CardBody>
                    <Row noGutters>
                        <Col xs="auto" className="mr-2 align-items-center">
                            <FontAwesomeIcon icon="cogs" />
                        </Col>
                        <Col className="d-flex align-items-center">
                            <h5 className="my-0">System Settings</h5>
                        </Col>
                        <Col xs="auto" className="d-flex align-items-center">
                            <h6 className="mb-0">{organization?.name}</h6>
                        </Col>
                    </Row>
                    
                </CardBody>
            </Card>
            
            <TimesheetSettings className="mt-3" />
            <WorkflowSettings className="mt-3" />
        </>
    )
}

export default Settings