////// Packages //////////////////
import React, {useState, useEffect} from 'react';
import { useDispatch, useSelector } from 'react-redux';

////// Local Components //////////
import GroupsTable from './components/GroupsTable';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { getAdminOrgEmployees } from 'store/thunks/users';
import useAction from 'store/hooks/useAction';

const UserGroups = () => {
    const [pageNumber, setPageNumber] = useState(0);
    const getOrgEmployees = useAction(getAdminOrgEmployees)
    const myOrgId = useSelector( state => state.app.login.organizationId)

    const numberPerPage = 100;
    // javascript here
    useEffect(() => {
        getOrgEmployees.post(myOrgId, pageNumber, numberPerPage)
    }, [])
    return (
        // jsx here
        <GroupsTable />
    )
}


export default UserGroups;