import React, {FC} from 'react';
import {  FormGroup, Label } from 'reactstrap';
import Select, {Props as SelectProps } from 'react-select';

export type FormGroupSelectOptions = { value: (string | number | readonly string[] | undefined), label: React.ReactNode }[]

interface Props extends SelectProps<{ label: string | number; value: string | number }, true> {
  id: string;
  label: string;
}

const FormGroupMultiSelect: FC<Props> = ({ id, label, styles, ...inputProps }) => (
  <FormGroup>
    {label && <Label for={id}>{label}</Label>}
      <Select 
        id={id} 
        isMulti={true} 
        styles={{
          ...styles,
          menu: provided => ({...provided, zIndex: 999})
        }}
        {...inputProps} />
      {/* //   {options && isIterableArray(options) &&
      //     options.map(({ value, label }, index) => (
      //       <option value={value} key={index} dangerouslySetInnerHTML={createMarkup(label)} />
      //     ))}
      // </Select> */}
  </FormGroup>
);

export default FormGroupMultiSelect;
