////// Packages //////////////////
import React, {useState, useEffect, FC } from 'react';
import { useSelector } from 'react-redux';
import { isEqual } from 'lodash';
import { FormGroup, Input, Label, Col, Row, Alert, Button } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendUserPermissions } from 'store/thunks/users';

interface Props {
    userId: string;
    editMode: boolean;
    setEditMode: (mode: boolean)=>void;
}

const UserPermissions: FC<Props> = ({userId, editMode, setEditMode}) => {
    const sendPermissions = useAction(sendUserPermissions); 
    const permissions = useSelector ( state => state.id.users[userId]?.explicitPermissions, isEqual);

    const [newPermissions, setNewPermissions] = useState([] as string[]);

    useEffect(() => {
        setNewPermissions(Array.from(permissions || []));
    }, [permissions]);

    const allPermissions: {[className: string]: string[]} = {
        "Administrative": [
            "Organization global administrator", 
            "Edit the organization",
            "Edit system settings",
            "Create new users", 
            "Create a permissions group",
            "Add employees to a permission group"
        ],
        
        "Managerial": [
            "Assign employees to managers",
            "Edit any timesheet",
            "Edit closed timesheets",
            "Create new workflows",
            "Edit any workflow's information",
            "Edit any workflow's milestones and tasks",
            "View all time charged",
            "Generate administrative reports"
        ],
        
        "Financial": [
            "View any contract or workflow budget information",
            "Approve any expense",
            "Approve any timesheet",
            "Approve any time off",
            "Create new contracts",
            "Edit new contracts",
            "View generated invoices",
            "Edit generated invoices",
            "Approve generated invoices"
        ],
        
        "Other": [
            "Create assignments",
            "Post comments to any workflow",
            "Create and edit contacts' information"
        ]
    };
    
    const checkmarkChanged = (value: string) => (event: React.ChangeEvent<HTMLInputElement> ) => {
        if (event.target.checked) {
            // do things when you check it
            const permissionsSet = new Set(newPermissions);
            permissionsSet.add(value)
            setNewPermissions(Array.from(permissionsSet));
        } else {
            // do things when you uncheck it
            const filteredPermissions = newPermissions.filter( v => v !== value );
            setNewPermissions(filteredPermissions);
        }
    }

    const permissionsCheckboxes = Object.keys(allPermissions).map( (heading, index) => (
        <div key={index}>

        <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">{heading}</h6>
            
            {allPermissions[heading].map((value, indx, arr) => (
                <FormGroup key={indx} className={`form-check ${indx === arr.length-1 ? "mb-4" : "mb-2"}`}>
                    <Input type="checkbox" name="check" id={`example${indx}`} checked={newPermissions.includes(value)} onChange={checkmarkChanged(value)} />
                    <Label for={`example${indx}`} check>
                        {value}
                    </Label>
                </FormGroup>
            ))}
        </div>
    ))
    
    const handleSubmit = async () => {
        const data = await sendPermissions.post(newPermissions, userId);
        if (data?.success) setEditMode(false);
    }
    
    return (
        <>
            
            { editMode ? 
                    <>
                        <Row>
                            <Col lg>
                                {permissionsCheckboxes.slice(0, permissionsCheckboxes.length/2)}
                            </Col>
                            <Col lg>
                                {permissionsCheckboxes.slice( permissionsCheckboxes.length/2 )}
                            </Col>
                        </Row>
                        {sendPermissions.error && <Alert color="danger">{sendPermissions.error}</Alert> }
                        <Row>
                            <Col>
                                <Button color="primary" className="d-flex ml-auto" onClick={ handleSubmit } >
                                    Save Changes
                                </Button>
                            </Col>
                        </Row>
                    </>
                    :
                    <ul>
                        {permissions && permissions.length > 0 ? 
                        permissions.map( (permission, index) => (
                                <li key={index}>{permission}</li>    
                            ))
                            :
                            <li>None</li>
                        }
                    </ul>
                    
                }    
        </>
    )
};

export default UserPermissions;