import { MExpense, MExpenseReport, MLedger, MWage } from "store/types/models/finance";
import { MPerson } from "store/types/models/id";
import {wages as wagesDuck, expenses as expensesDuck} from 'store/ducks/finance'
import { BaseDuck } from "store/ducks/BaseDuck";
import { ActionCreator } from "store/types/interface/common";
import {ledgers as ledgerDuck} from 'store/ducks/finance'

interface WageController extends BaseDuck<MWage> {
    createWage: ActionCreator<['createWage']>;
    getPersonWages: ActionCreator<['getPersonWages']>
    deleteWage: ActionCreator<['deleteWage']>
}

interface ExpenseController extends BaseDuck<MExpenseReport> {
    getExpense: ActionCreator<['getExpense']>
}

interface LedgerController extends BaseDuck<MLedger> {
    getLedger: ActionCreator<['getLedger']>
    getLedgers: ActionCreator<['getLedgers']>
    createLedger: ActionCreator<['createLedger']>
}

export const wages = wagesDuck as WageController
export const expenses = expensesDuck as ExpenseController
export const ledger = ledgerDuck as LedgerController

interface CreateWageProps {
    personId: MPerson["_id"], 
    effectiveDate: string, 
    wageAmount: number, 
    wageType: string | MWage['wageType'], 
    salaryPeriod: string | MWage['salaryPeriod'], 
    hourlyRate: number, 
    overtimeEligible?: boolean, 
    doubleTimeEligible?: boolean
}

export const createWage: ActionCreator<['createWage']> = (wage: CreateWageProps) => {
    const actions = [
        {
            name: 'createWage',
            data: wage
        }
    ] as const;
    return { actions }
}
wages.createWage = createWage


export const getPersonWages: ActionCreator<['getPersonWages']> = (personId: MPerson['_id']) => {
    const actions = [{
        name: 'getPersonWages',
        data: {personId}
    }] as const;
    return { actions }
}

wages.getPersonWages = getPersonWages

export const deleteWage: ActionCreator<['deleteWage']> = (wageId: MWage['_id']) => {
    const actions = [{
        name: "deleteWage",
        data: {wageId}
    }] as const;
    return { actions }
}

wages.deleteWage = deleteWage


////////// expenses thunks ////////////////////

export const getExpense: ActionCreator<['getExpense']> = (expenseId: MExpenseReport['_id']) => {
    const actions = [{
        name: "getExpense",
        data: {expenseId}
    }] as const;
    return { actions }
}
expenses.getExpense = getExpense

///////// ledger thunks //////////////////////

export const getLedger: ActionCreator<['getLedger']> = (ledger: MLedger) => {
    const actions = [{
        name: 'getLedger',
        data: {ledger}
    }] as const;
    return {actions}
}

ledger.getLedger = getLedger

export const getLedgers: ActionCreator<['getLedgers']> = () => {
    const actions = [{
        name: 'getLedgers',
        data: {}
    }] as const;
    return {actions}
}

ledger.getLedgers = getLedgers

export const createLedger: ActionCreator<['createLedger']> = (classification: string, subClassification: string, name: string) => {
    const actions = [{
        name: 'createLedger',
        data: {
            name,
            classification,
            subClassification
        }
    }] as const;
    return {actions}
}

ledger.createLedger = createLedger
