////// Packages //////////////////
import React, { FC, useMemo } from 'react'
import { InputProps } from 'reactstrap'

////// Local Components //////////

////// Global Components /////////
import withValueMask from 'hoc/withValueMask';
import FormGroupInput from './FormGroupInput';

////// CommonJS Helpers //////////
import {currencyFormat} from 'environ'

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props extends InputProps{
    locale?: string;
    currency?: string;
}

const CurrencyInput: FC<Props> = ({locale='en-US', currency='USD', ...props}) => {
    
    const FormattedInput = useMemo(
        () => withValueMask(FormGroupInput, val=>{
            const float = parseFloat(val)
            if (isNaN(float)) {
                return val

            } 
            return currencyFormat.format(float)
        })
    , [locale, currency])

    return (
        <FormattedInput autoComplete='off' {...props}/>
    )
}

export default CurrencyInput