////// Packages //////////////////
import React, {useState, useCallback, FC} from 'react';
import { Modal, ModalHeader, ModalBody, Button, Alert, Row, Col, Spinner, ModalFooter } from 'reactstrap';
import { toast } from 'react-toastify';

////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { addOrganizationUser } from 'store/thunks/users';


interface Props {
    openModal: boolean;
    setOpenModal: (open: boolean)=>void;
}
const AddUserModal: FC<Props> = ({openModal, setOpenModal}) => {
    const addUser = useAction(addOrganizationUser);
    
    const [email, setEmail] = useState('');
    const [position, setPosition] = useState('');
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');

    const toggle = useCallback(() => setOpenModal(!openModal), [openModal]);
    const emailChange = useCallback((event) => setEmail(event.target.value), [setEmail]);
    const positionChange = useCallback((event) => setPosition(event.target.value), [setPosition]);
    const firstNameChange = useCallback((event) => setFirstName(event.target.value), [setFirstName]);
    const lastNameChange = useCallback((event) => setLastName(event.target.value), [setLastName])
    
    const handleSubmit = useCallback(async () => {
        const data = await addUser.post(firstName, lastName, email, position);
        if (data?.success) {
            toast.success('Successfully added user ' + email);
            setEmail('');
            setPosition('');
            setFirstName('');
            setLastName('');
            setOpenModal(false);
        }
    }, [firstName, lastName, email, position])

    const handleCancel = useCallback(async () => {
            setEmail('');
            setPosition('');
            setFirstName('');
            setLastName('');
            setOpenModal(false);
    }, [])
    
    return (
        <Modal 
            isOpen={openModal} 
            toggle={toggle} 
        >
            
            <ModalHeader className="bg-light border-bottom-0">
                Invite a user to your organization
            </ModalHeader>
            <ModalBody>
                <FormGroupInput 
                    id="firstName" 
                    type="text" 
                    name="first-name"
                    label="First Name" 
                    valid={!!firstName} 
                    value={firstName} 
                    onChange={firstNameChange}
                    placeholder="First Name"
                />
                <FormGroupInput 
                    id="lastName" 
                    type="text" 
                    name="last-name"
                    label="Last Name" 
                    valid={!!lastName} 
                    value={lastName} 
                    onChange={lastNameChange}
                    placeholder="Last Name"
                />
                <FormGroupInput 
                    id="email" 
                    type="email" 
                    label="Email" 
                    valid={!!email} 
                    value={email} 
                    onChange={emailChange} 
                    placeholder="email@example.com"
                />
                <FormGroupInput 
                    id="position" 
                    type="text" 
                    name="position"
                    label="Position" 
                    valid={!!position} 
                    value={position} 
                    onChange={positionChange}
                    placeholder="Job Title" 
                />

                {addUser.error && <Alert color='danger'>{addUser.error}</Alert>}

                
            </ModalBody>
            <ModalFooter className="bg-light border-top-0">
                <Row>
                    <Col xs="auto" className="ml-auto">
                        <Button size="sm" color="primary" onClick={handleSubmit} >
                            {addUser.loading ? 
                                <Spinner size="sm" color="light" />
                            :
                                <>Submit</>
                            }
                        </Button>
                        <Button size="sm" color="secondary" onClick={handleCancel} className="ml-2">
                            Cancel
                        </Button>
                    </Col>
                </Row>
            </ModalFooter>
        </Modal>
    )
}

export default AddUserModal;