////// Packages //////////////////
import React, { createRef, Fragment, useCallback, useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  Col,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Row,
  UncontrolledDropdown,
  Modal,
  ModalBody,
  ModalHeader
} from 'reactstrap';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
import BootstrapTable from 'react-bootstrap-table-next';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';
import { isEqual } from 'lodash';
import {useDispatch, useSelector} from 'react-redux';
import { toast } from 'react-toastify';

////// Local Components //////////
import AddUserModal from '../AddGroupModal';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import ButtonIcon from 'components/ButtonIcon';

////// CommonJS Helpers //////////
import { getPaginationArray } from 'helpers/utils';

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { removeOrganizationUser } from 'store/thunks/users';

////// Types /////////////////////
import { IEmail, IPhone } from 'store/types/models/id';


const nameFormatter = (dataField, { _id, title }) => (
    <Link to={`/admin/user-details/${_id}`}>
      <h5 className="mb-0 fs--1">{title}</h5>
    </Link>
);

const emailFormatter = (email: IEmail) => <a href={`mailto:${email}`}>{email}</a>;
const phoneFormatter = (phone: IPhone) => <a href={`tel:${phone}`}>{phone}</a>;

const dateFormatter = (date: string) => date ? dayjs(date).format('MM/DD/YYYY') : 'unknown';

const activeFormatter = (active: boolean) => {
    if (active) {
        return (
            <FontAwesomeIcon icon='user-check' className='text-primary fs-1' />
        )
    }
    return (
        <FontAwesomeIcon icon='user-times' className='text-secondary fs-1' />
    )
}



const GroupsTable = () => {
  const [openModal, setOpenModal] = useState(false);
  const [openRemoveModal, setRemoveModal] = useState(false);
  const [personToRemove, setPersonToRemove] = useState();

  const users = useSelector( state => state.id.users, isEqual );
  const dispatch = useDispatch();

  const handleOpen = useCallback( () => setRemoveModal(true), [ setRemoveModal ] );
  const handleClose = useCallback( () => setRemoveModal(false), [ setRemoveModal ] );
  const toggle = useCallback( () => setRemoveModal( !openRemoveModal ), [ openRemoveModal, setRemoveModal ] );

  const handleRemovePerson = useCallback(async () => {
      if (personToRemove) {
        const data = await dispatch(removeOrganizationUser(personToRemove));
        setPersonToRemove(undefined);
        toast.success('Successfully removed user');
        setRemoveModal(false);
      }
  }, [personToRemove])

  
  const table = createRef();
  
  const arrUsers = Object.values(users)
  
  const actionFormatter = (dataField, { id }) => {
    
    // Control your row with this id
    return (
      <UncontrolledDropdown>
        <DropdownToggle color="link" size="sm" className="text-600 btn-reveal mr-3">
          <FontAwesomeIcon icon="ellipsis-h" className="fs--1" />
        </DropdownToggle>
        <DropdownMenu right className="border py-2">
          <DropdownItem onClick={ () => {
              setPersonToRemove(dataField)
              setRemoveModal(true) 
          } } className="text-danger">
            Remove User
          </DropdownItem>
        </DropdownMenu>
      </UncontrolledDropdown>
    )
  };

  const columns = [
    {
      dataField: 'title',
      text: 'Title',
      headerClasses: 'border-0',
      classes: 'border-0 py-2 align-middle',
      formatter: nameFormatter,
      sort: true
    },
    {
      dataField: 'email',
      headerClasses: 'border-0',
      text: 'Email Address',
      classes: 'border-0 py-2 align-middle',
      formatter: emailFormatter,
      sort: true
    },
    {
      dataField: 'position',
      headerClasses: 'border-0',
      text: 'Position',
      classes: 'border-0 py-2 align-middle',
      sort: true
    },
    {
      dataField: 'startDate',
      headerClasses: 'border-0',
      text: 'Start Date',
      classes: 'border-0 py-2 align-middle',
      formatter: dateFormatter,
      sort: true
    },
    {
      dataField: 'active',
      headerClasses: 'border-0',
      text: 'Active',
      classes: 'border-0 py-2 align-middle',
      formatter: activeFormatter,
      sort: true
    },
    {
      dataField: 'personId',
      headerClasses: 'border-0',
      text: '',
      classes: 'border-0 py-2 align-middle',
      formatter: actionFormatter,
      align: 'right'
    }
  ];

  const options = {
    custom: true,
    sizePerPage: 12,
    totalSize: arrUsers.length || 1
  };
  // State
  const handleNextPage = ({ page, onPageChange }) => () => {
    onPageChange(page + 1);
  };

  const handlePrevPage = ({ page, onPageChange }) => () => {
    onPageChange(page - 1);
  };

  return (
    <Card className="mb-3">
      <Modal centered size="sm" isOpen={ openRemoveModal } toggle={toggle}>
        <ModalHeader className="bg-light justify-content-center">
          Are you sure? 
        </ModalHeader>
        <ModalBody>
          <h6 className="pt-3 pb-3 col-xs-1 text-center"> This action cannot be undone.</h6>
          <div className="d-flex justify-content-center">
            <Button color="danger" onClick={ handleRemovePerson }  >
              Remove
            </Button>
            <Button color="secondary" onClick={ handleClose } className="ml-3" >
              Cancel
            </Button>
          </div>
        </ModalBody>
      </Modal>
      <FalconCardHeader title="User Groups" light={false}>
        <ButtonIcon
            icon="plus"
            transform="shrink-3 down-2"
            color="falcon-default"
            size="sm"
            onClick={() => setOpenModal(true)}
        >
            New
        </ButtonIcon>
        <AddUserModal openModal={openModal} setOpenModal={setOpenModal} />
        <ButtonIcon 
            icon="filter" 
            transform="shrink-3 down-2" 
            color="falcon-default" 
            size="sm" 
            className="mx-2"
        >
            Filter
        </ButtonIcon>
        <ButtonIcon 
            icon="external-link-alt" 
            transform="shrink-3 down-2" 
            color="falcon-default" 
            size="sm"
        >
            Export
        </ButtonIcon>
      </FalconCardHeader>
      <CardBody className="p-0">
        <PaginationProvider pagination={paginationFactory(options)}>
          {({ paginationProps, paginationTableProps }) => {
            const lastIndex = paginationProps.page * paginationProps.sizePerPage;
            return (
              <Fragment>
                <div className="table-responsive">
                  <BootstrapTable
                    ref={table}
                    bootstrap4
                    keyField="id"
                    data={arrUsers}
                    columns={columns}
                    bordered={false}
                    classes="table-dashboard table-striped table-sm fs--1 border-bottom border-200 mb-0 table-dashboard-th-nowrap"
                    rowClasses="btn-reveal-trigger border-top border-200"
                    headerClasses="bg-200 text-900 border-y border-200"
                    {...paginationTableProps}
                  />
                </div>
                <Row noGutters className="px-1 py-3 flex-center">
                  <Col xs="auto">
                    <Button
                      color="falcon-default"
                      size="sm"
                      onClick={handlePrevPage(paginationProps)}
                      disabled={paginationProps.page === 1}
                    >
                      <FontAwesomeIcon icon="chevron-left" />
                    </Button>
                    {getPaginationArray(paginationProps.totalSize, paginationProps.sizePerPage).map(pageNo => (
                      <Button
                        color={paginationProps.page === pageNo ? 'falcon-primary' : 'falcon-default'}
                        size="sm"
                        className="ml-2"
                        onClick={() => paginationProps.onPageChange(pageNo)}
                        key={pageNo}
                      >
                        {pageNo}
                      </Button>
                    ))}
                    <Button
                      color="falcon-default"
                      size="sm"
                      className="ml-2"
                      onClick={handleNextPage(paginationProps)}
                      disabled={lastIndex >= paginationProps.totalSize}
                    >
                      <FontAwesomeIcon icon="chevron-right" />
                    </Button>
                  </Col>
                </Row>
              </Fragment>
            );
          }}
        </PaginationProvider>
      </CardBody>
    </Card>
  );
};

export default GroupsTable;
