////// Packages //////////////////
import React, { useState } from 'react';
import { Card, CardBody } from 'reactstrap';
import { useParams } from 'react-router-dom';

////// Local Components //////////
import PersonalInfo from './components/PersonalInfo';
import UserWages from './components/UserWages';
import UserHeader from './components/UserHeader';
import UserPermissions from './components/UserPermissions';
import UserTimeOff from './components/UserTimeOff';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import ButtonIcon from 'components/ButtonIcon';
import useUser from 'store/hooks/user/useUser';
import LoadingGateway from 'components/LoadingGateway';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const UserDetails = () => {
    const { userId } = useParams<{userId: string}>();

    const { value: user, ...userLoader } = useUser( userId )
    const [editPersonalInfo, setEditPersonalInfo] = useState(false);
    const [editUserWages, setEditUserWages] = useState(false);
    const [editPermissions, setEditPermissions] = useState(false);
    const [editTimeOff, setEditTimeOff] = useState(false);

    return (
        <LoadingGateway dataLoader={userLoader}>
            
            <Card className="mb-3">
                <UserHeader userId={ userId } />
            </Card>
            <Card className="mb-3">
                <FalconCardHeader title="Personal Information">
                    <ButtonIcon 
                        color="falcon-default" 
                        size="sm" 
                        icon={editPersonalInfo ? "times" : "pencil-alt"}  
                        iconClassName="fs--2" 
                        onClick={ () => setEditPersonalInfo(!editPersonalInfo) }
                    >
                        {editPersonalInfo ? "Cancel" : "Update"}
                    </ButtonIcon>
                </FalconCardHeader>
                <CardBody className="border-top">
                    <PersonalInfo userId={userId} editMode={editPersonalInfo} setEditMode={setEditPersonalInfo} />
                </CardBody>
            </Card>
            <Card className="mb-3">
                <FalconCardHeader title="Wages">
                    <ButtonIcon 
                            color="falcon-default" 
                            size="sm" 
                            icon={editUserWages ? "times" : "pencil-alt"}  
                            iconClassName="fs--2"
                            onClick={ () => setEditUserWages(!editUserWages) } 
                    >
                        {editUserWages ? "Cancel" : "Update"}
                    </ButtonIcon>
                </FalconCardHeader>
                <CardBody className="border-top">
                    <UserWages userId={userId} editMode={editUserWages} setEditMode={setEditUserWages} />
                </CardBody>
            </Card>
            <Card className="mb-3">
                <FalconCardHeader title="Time Off">
                    <ButtonIcon 
                            color="falcon-default" 
                            size="sm" 
                            icon={editTimeOff ? "times" : "pencil-alt"}  
                            iconClassName="fs--2"
                            onClick={ () => setEditTimeOff(!editTimeOff) } 
                    >
                        {editTimeOff ? "Cancel" : "Update"}
                    </ButtonIcon>
                </FalconCardHeader>
                <CardBody className="border-top">
                    <UserTimeOff userId={userId} editMode={editTimeOff} setEditMode={setEditTimeOff} />
                </CardBody>
            </Card>
            <Card className="mb-3">
                <FalconCardHeader title="Permissions">
                    <ButtonIcon
                        color="falcon-default" 
                        size="sm" 
                        icon={editPermissions ? "times" : "pencil-alt"}  
                        iconClassName="fs--2"
                        onClick = { () => setEditPermissions(!editPermissions) }
                    >
                        {editPermissions ? "Cancel" : "Update"}
                    </ButtonIcon>
                </FalconCardHeader>
                <CardBody className="border-top">
                    <UserPermissions userId={userId} editMode={editPermissions} setEditMode={setEditPermissions} />
                </CardBody>
            </Card>
        </LoadingGateway>
    )
}

export default UserDetails;