////// Packages //////////////////
import React, { useCallback, useMemo, useRef, useState } from 'react';
import {
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  UncontrolledDropdown,
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import dayjs from 'dayjs';
import { isEqual } from 'lodash';
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import confirm from 'reactstrap-confirm'

////// Local Components //////////
import AddUserModal from '../AddUserModal';

////// Global Components /////////

////// CommonJS Helpers //////////
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { removeOrganizationUser } from 'store/thunks/users';
import { MPerson, MUser } from 'store/types/models/id';
import PaginatingTableCard from 'compositions/table/PaginatingTableCard';
import NameFormatter from 'compositions/NameFormatter';

// import customers from '../../data/e-commerce/customers';


const emailFormatter = (email: string) => <a href={`mailto:${email}`}>{email}</a>;
// const phoneFormatter = (phone: string) => <a href={`tel:${phone}`}>{phone}</a>;

const dateFormatter = (date?: string) => date ? dayjs(date).format('MM/DD/YYYY') : 'unknown';

const activeFormatter = (active?: boolean) => {
  if (active) {
    return (
      <FontAwesomeIcon icon='user-check' className='text-primary fs-1' />
    )
  }
  return (
    <FontAwesomeIcon icon='user-times' className='text-secondary fs-1' />
  )
}

const dateCSVFormatter = (date: string) => (date && dayjs(date).isValid()) ? dayjs(date).format('MM/DD/YYYY') : '';

///////////////////// MAIN COMPONENT /////////////////////////////////
const UsersTable = () => {
  const [openModal, setOpenModal] = useState(false);
  const removeUser = useAction(removeOrganizationUser);

  const rawUsers = useSelector(state => state.id.users, isEqual);
  const people = useSelector( state => state.id.people );

  const users = useMemo(() => Object.values(rawUsers).map(user => {
    const person = people[user.personId]
    return {
      ...user,
      name: person?.name,
      position: person?.position,
      personId: person?._id,
      avatar: person?.avatar
    }
  }), [rawUsers, people])

  const handleRemovePerson = useCallback(async (_id) => {
    if (!_id) return;
    const shouldRemove = await confirm({
      message: "Are you sure you want to remove this user?",
      confirmText: "Remove",
      confirmColor: "danger",
      cancelText: "Cancel",
      cancelColor: "secondary"
    })
    if (!shouldRemove) return
    const data = await removeUser.post(_id);
    if (data?.success) {
      toast.success('Successfully removed user');
    } else {
      toast.error(data?.error || 'Something went wrong')
    }
  }, [removeUser.post])

  const actionFormatter = useCallback((_id: string) => (
    <UncontrolledDropdown>
      <DropdownToggle color="link" size="sm" className="text-600 btn-reveal mr-3">
        <FontAwesomeIcon icon="ellipsis-h" className="fs--1" />
      </DropdownToggle>
      <DropdownMenu right className="border py-2">
        <DropdownItem onClick={() => handleRemovePerson(_id)} className="text-danger">
          Remove User
        </DropdownItem>
      </DropdownMenu>
    </UncontrolledDropdown>
  ), [handleRemovePerson])
  type RowData = MUser & { name: MPerson['name'], avatar: MPerson['avatar'] }
  const columns = [
    {
      dataField: 'name.last',
      text: 'User',
      csvText: 'Full Name',
      csvFormatter: (_: any, user: RowData) => makeName(user.name, { first: true, middle: true, last: true, prefix: true, title: true, reverse: true }),
      headerClasses: 'border-0',
      classes: 'border-0 py-2 align-middle',
      formatter: (_: any, user: RowData) => <NameFormatter {...user} />,
      sort: true,
      sortFunc: (_: any, _2: any, order: 'asc' | 'desc', _3: any, rowA: RowData, rowB: RowData) => {
        const nameA = makeName(rowA.name, {first: true, common: true, last: true, reverse: true})
        const nameB = makeName(rowB.name, {first: true, common: true, last: true, reverse: true})
        if (order === 'asc') {
          return nameA < nameB ? -1 : 1
        }
        return nameB < nameA ? -1 : 1
      },
      search: (data: typeof users[0]) => makeName(data.name, {flat: true})
    },
    {
      dataField: 'email',
      headerClasses: 'border-0',
      text: 'Email Address',
      classes: 'border-0 py-2 align-middle',
      formatter: emailFormatter,
      sort: true,
      search: true
    },
    {
      dataField: 'position',
      headerClasses: 'border-0',
      text: 'Position',
      classes: 'border-0 py-2 align-middle',
      sort: true,
      search: true
    },
    {
      dataField: 'startDate',
      headerClasses: 'border-0',
      text: 'Start Date',
      classes: 'border-0 py-2 align-middle',
      formatter: dateFormatter,
      csvFormatter: dateCSVFormatter,
      sort: true
    },
    {
      dataField: 'active',
      headerClasses: 'border-0',
      text: 'Active',
      classes: 'border-0 py-2 align-middle',
      formatter: activeFormatter,
      sort: true
    },
    {
      dataField: 'personId',
      headerClasses: 'border-0',
      text: '',
      classes: 'border-0 py-2 align-middle',
      formatter: actionFormatter,
      align: 'right',
      csvExport: false
    }
  ];

  const csvExportRef = useRef<any>()

  return (
    <>
      <AddUserModal openModal={openModal} setOpenModal={setOpenModal} />
      
      <PaginatingTableCard
        title="Users"
        cardClass="mb-3"
        paginationOptions={{
          sizePerPage: 12
        }}
        csvExportRef={csvExportRef}
        exportCSV={{fileName: "Happin Users Export " + dayjs().format("MM-DD-YYYY") + '.csv'}}
        data={users}
        columns={columns}
        handleNew={()=>setOpenModal(true)}
        search
      />
    </>
  );
};

export default UsersTable;

