import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import Organization from 'layouts/administration/organization/Organization';
import OrgUsers from 'layouts/administration/users/OrgUsers';
import UserDetails from 'layouts/administration/users/UserDetails';
import UserGroups from 'layouts/administration/groups/UserGroups';
import Settings from 'layouts/administration/settings/Settings';

const AdminRoutes = ({ match: { url } }) => (
  <Switch>
    <Route path={`${url}/organization`} component={Organization} />
    <Route path={`${url}/users`} exact component={OrgUsers} />
    <Route path={`${url}/user-details/:userId`} exact component={UserDetails} />
    <Route path={`${url}/user-groups`} exact component={UserGroups} />
    <Route path={`${url}/settings`} exact component={Settings} />
    {/* <Route path={`${url}/chat`} exact component={Chat} /> */}

    {/*Redirect*/}
    <Redirect to="/errors/404" />
  </Switch>
);

export default AdminRoutes;
