////// Packages //////////////////
import React, {useState, useCallback, FC} from 'react';
import { Modal, ModalHeader, ModalBody, Button, Alert, Row, Col, Label } from 'reactstrap';
import {useDispatch} from 'react-redux';
import { toast } from 'react-toastify';

////// Local Components //////////
import GroupPermissions from './components/GroupPermissions';

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// ????? //////////////


interface Props {
    openModal: boolean;
    setOpenModal: (open: boolean)=>void;
    userId: string;
}

const AddGroupModal: FC<Props> = ({openModal, setOpenModal, userId}) => {
    const dispatch = useDispatch();
    const addUserGroup = (name: string) => {return {success: true, name}}
    const [groupName, setGroupName] = useState('');

    const [error, setError] = useState('');

    const toggle = useCallback(() => setOpenModal(!openModal), [openModal]);
    const groupNameChange = useCallback((event) => setGroupName(event.target.value), [setGroupName]);
    
    //need to add permissions to handlesubmit and cancel
    const handleSubmit = useCallback(async () => {
        const data = await dispatch(addUserGroup(groupName));
        if (data?.success) {
            toast.success('Successfully created new group');
            setGroupName('');
            setOpenModal(false);
        } else {
            setError('Something went wrong!')
        }
    }, [groupName])

    const handleCancel = useCallback(async () => {
            setGroupName('');
            setOpenModal(false);
    }, [])
    
    return (
        <Modal 
            isOpen={openModal} 
            toggle={toggle} 
        >
            
            <ModalHeader >
                Create a new group
            </ModalHeader>
            <ModalBody className="bg-light">
                <FormGroupInput 
                    id="groupName" 
                    type="text" 
                    label="Group Name" 
                    valid={!!groupName} 
                    value={groupName} 
                    onChange={groupNameChange}
                    placeholder="Group Name"
                />
                <Label>Group Permissions</Label>
                <GroupPermissions userId={userId} />

                {error && <Alert color='danger'>{error}</Alert>}

                <Row>
                    <Col xs="auto" className="ml-auto">
                        <Button size="sm" color="primary" onClick={handleSubmit} >
                            Submit
                        </Button>
                        <Button size="sm" color="secondary" onClick={handleCancel} className="ml-2">
                            Cancel
                        </Button>
                    </Col>
                </Row>
            </ModalBody>
            
        </Modal>
    )
}

export default AddGroupModal;