////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { FC, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import { toast } from 'react-toastify'
import { Badge, Button, Card, CardBody, CardHeader, Col, CustomInput, DropdownItem, DropdownMenu, DropdownToggle, Modal, ModalBody, ModalFooter, ModalHeader, Row, UncontrolledDropdown, UncontrolledTooltip } from 'reactstrap'
import confirm from 'reactstrap-confirm';

////// Local Components //////////

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader'
import FormGroupInput from 'components/FormGroupInput'
import FormGroupSelect from 'components/FormGroupSelect'

////// CommonJS Helpers //////////
import {colorOptions} from 'components/falconTypes';
 
////// Custom Hooks //////////////
import useTimeCodes from 'store/hooks/time/useTimeCodes'
import useAction from 'store/hooks/useAction'

////// Thunks ////////////////////
import { createTimeCode, createTimeType, deleteTimeType, updateTimeType } from 'store/thunks/time'

////// Types /////////////////////
import { MTimeType } from 'store/types/models/time'
import { chunk } from 'lodash'

interface Props {
    className?: string;
}

const TimesheetSettings: FC<Props> = ({className}) => {
    const createTypeAction = useAction(createTimeType)
    const createCodeAction = useAction(createTimeCode)
    const deleteTypeAction = useAction( deleteTimeType )
    const updateTimeTypeAction = useAction( updateTimeType )
    const [isOpenCreateTypeModal, setIsOpenCreateTypeModal] = useState(false);
    const [typeTitle, setTypeTitle] = useState('');
    const [typeColor, setTypeColor] = useState<string | undefined>(undefined);
    const [newTypeIsWorkflow, setNewTypeIsWorkflow] = useState(false);

    const [isOpenCreateCodeModal, setIsOpenCreateCodeModal] = useState(false);
    const [codeTitle, setCodeTitle] = useState('');
    const [newCodeIsBankable, setNewCodeIsBankable] = useState(false);

    const {value: timeTypes, ...timeTypesLoader} = useTimeCodes()
    const [selectedType, setSelectedType] = useState('');

    const timeCodes = timeTypes?.find( type => type._id === selectedType )?.timeCodes;

    async function handleSubmitType() {
        const data = await createTypeAction.post(typeTitle, typeColor, newTypeIsWorkflow);
        if (data?.success) {
            toast.success('created new type ' + typeTitle)
            setTypeTitle('')
            setTypeColor(undefined);
            setNewTypeIsWorkflow(false)
            setIsOpenCreateTypeModal(false)
            timeTypesLoader.force();
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }

    async function handleSubmitCode() {
        const data = await createCodeAction.post(codeTitle, selectedType, newCodeIsBankable);
        if (data?.success) {
            toast.success('created new code ' + typeTitle)
            setCodeTitle('')
            setNewCodeIsBankable(false)
            setIsOpenCreateCodeModal(false)
            timeTypesLoader.force();
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }

    function indication() {
        return (
            <Card onClick={() => setIsOpenCreateTypeModal(true)} className="cursor-pointer">
                <CardBody>
                    Add time category...
                </CardBody>
            </Card>
        )
    }

    async function handleDeleteType(_id: MTimeType['_id']) {
        const confirmed = await confirm({
            message: 'Are you sure you want to delete this type?',
            confirmText: 'Delete',
            confirmColor: 'danger',
            cancelText: 'Cancel',
            cancelColor: 'secondary'
        })
        if (!confirmed) return

        const data = await deleteTypeAction.post(_id);
        if (data?.success) {
            toast.success("deleted")
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }

    async function handleSetTimeTypeColor(eventColor: MTimeType['eventColor'], timeTypeId: MTimeType['_id']) {
        const data = await updateTimeTypeAction.post(timeTypeId, {eventColor})
        if (data?.success) {
            toast.success('color successfully updated')
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }

    async function handleSetTimeTypeIsWorkflow(isWorkflow: MTimeType['isWorkflow'], timeTypeId: MTimeType['_id']) {
        const data = await updateTimeTypeAction.post(timeTypeId, {isWorkflow})
        if (data?.success) {
            toast.success('time type workflow status successfully updated')
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }

    const eventColorFormatter = (cell: MTimeType['eventColor'], row: MTimeType) => {
        const filteredColors = colorOptions.filter(option => option.value !== cell);
        const selectedColor = colorOptions.find( option => option.value === cell )
        const pickerOptions = selectedColor ? filteredColors.concat(selectedColor) : filteredColors
        return (
        <UncontrolledDropdown>
            <DropdownToggle className={`badge-${cell} fs--1`} caret>
                    { selectedColor?.label }
            </DropdownToggle>
            <DropdownMenu style={{position: 'absolute'}}>
                {
                    chunk(pickerOptions, 4).map( (group, index1) => (
                        <Row noGutters key={index1}>
                            {
                                group.map( (option, index2) => (
                                    <Col key={index2} className={`badge-${option.value} p-1 m-2 border`} onClick={()=>handleSetTimeTypeColor(option.value as MTimeType['eventColor'], row._id)}>
                                        <FontAwesomeIcon icon="paint-brush" />
                                    </Col>
                                ))
                            }
                        </Row>
                        
                       
                    ))
                }
            </DropdownMenu>
            
        </UncontrolledDropdown>
    )}

    return (
        <Card className={className}>
            <FalconCardHeader
                title="Timesheet Settings"
            />
            <CardBody className="bg-light">
                <Row>
                    <Col lg>
                        <Card>
                            <CardHeader >
                                <Row>
                                    <Col>
                                        <h5>Time Categories</h5>
                                    </Col>
                                    <Col xs="auto">
                                        <Button size="sm" color="falcon-primary" onClick={()=>setIsOpenCreateTypeModal(true)} id="add-time-category">
                                            <FontAwesomeIcon icon="plus" /> 
                                        </Button>
                                        <UncontrolledTooltip target="add-time-category" placement="top">
                                                            Add Time Category
                                        </UncontrolledTooltip>
                                        <Modal isOpen={isOpenCreateTypeModal} toggle={()=>setIsOpenCreateTypeModal(o=>!o)}>
                                            <ModalHeader className="bg-light border-bottom-0">
                                                Create Type
                                            </ModalHeader>
                                            <ModalBody>
                                            
                                                <FormGroupInput
                                                    id="new-type-title"
                                                    label="Title"
                                                    onChange={ e=>setTypeTitle(e.target.value) }
                                                    value={typeTitle}
                                                    invalid={ !!timeTypes?.find( type => type.title === typeTitle ) }
                                                    feedback={ 'That title is already used'}
                                                />
                                                <FormGroupSelect
                                                    id="new-type-color"
                                                    label="Event Color"
                                                    styles={{
                                                        menu: provided => ({...provided, zIndex: 999})
                                                    }}
                                                    options={colorOptions}
                                                    formatOptionLabel={option => <div className={`badge-${option.value} p-2 rounded w-100`}>{option.label}</div>} 
                                                    onChange={ e=>setTypeColor(e?.value) }
                                                    value={ colorOptions.find( c => c.value === typeColor ) }
                                                />
                                            </ModalBody>
                                            <ModalFooter className="d-flex border-top-0">
                                                <CustomInput
                                                    id="new-type-is-workflow"
                                                    type="checkbox"
                                                    style={{zIndex: 0}}
                                                    checked={newTypeIsWorkflow}
                                                    onChange={e => setNewTypeIsWorkflow(e.target.checked)}
                                                    label="This entry type includes workflow information"
                                                />
                                                <Button className="ml-auto" size="sm" color="primary" onClick={handleSubmitType}>Submit</Button>
                                            </ModalFooter>
                                        </Modal>
                                    </Col>
                                </Row>
                            </CardHeader>
                            {/* Time Categories Table */}
                            <BootstrapTable
                                classes="table-sm fs--1 border-bottom border-200 mb-0 table-dashboard-th-nowrap"
                                rowClasses="btn-reveal-trigger border-top border-200"
                                headerClasses="bg-200 text-900 border-y border-200"
                                bootstrap4
                                wrapperClasses="mb-3"
                                keyField="title"
                                data={timeTypes || []}
                                selectRow={{
                                    mode: 'radio',
                                    clickToSelect: true,
                                    hideSelectColumn: true,
                                    classes: 'bg-soft-primary',
                                    onSelect: (row)=>{setSelectedType(row._id)}
                                }}
                                columns={[
                                    {
                                        dataField: 'title', 
                                        text: 'Title', 
                                        formatter: (cell, row, _, selected) => <h6 onClick={()=>setSelectedType(row._id)}>{cell}</h6>, // eslint-disable-line react/display-name
                                        sort: true,
                                        headerAlign: 'left',
                                        headerClasses: 'border-0',
                                        classes: 'border-0 py-2 align-middle'
                                    },
                                    {
                                        dataField: 'eventColor',
                                        text: 'Event Color',
                                        formatter: eventColorFormatter, // eslint-disable-line react/display-name
                                        align: 'center',
                                        headerAlign: 'center',
                                        headerClasses: 'border-0',
                                        classes: 'border-0 py-2 align-middle'
                                    },
                                    {
                                        dataField: 'isWorkflow',
                                        text: 'Shows Workflows',
                                        formatter: (cell, row, _, selected) => <FontAwesomeIcon icon={cell ? 'check' : 'times'} className={cell ? 'text-success' : 'text-secondary'} onClick={()=>handleSetTimeTypeIsWorkflow(!cell, row._id)}/>, // eslint-disable-line react/display-name
                                        sort: true,
                                        align: 'center',
                                        headerAlign: 'center',
                                        headerClasses: 'border-0',
                                        classes: 'border-0 py-2 align-middle'
                                    },
                                    {
                                        dataField: "_id",
                                        text: "Delete",
                                        formatter: (cell, row) => <Button size="sm" color="falcon-danger" onClick={()=>handleDeleteType(cell)}><FontAwesomeIcon icon="trash-alt" /></Button>, // eslint-disable-line react/display-name
                                        align: 'center',
                                        headerAlign: 'center'
                                    }
                                ]}
                                hover
                                noDataIndication={indication}
                                tabIndexCell
                                defaultSortDirection="asc"
                                defaultSorted={[{
                                    dataField: 'title',
                                    order: 'asc'
                                  }]}
                                rowStyle={{cursor: 'pointer'}}
                                
                            />
                        </Card>
                    </Col>
                    

                    <Col lg>
                        <Card className="mt-3 mt-lg-0">
                            { selectedType 
                                ? (
                                        <>
                                            <CardHeader>
                                                <Row>
                                                    <Col>
                                                        <h5>Time Codes</h5>
                                                    </Col>
                                                    <Col xs="auto">
                                                        <Button size="sm" color="falcon-primary" onClick={()=>setIsOpenCreateCodeModal(true)} id="add-time-codes">
                                                            <FontAwesomeIcon icon="plus" />
                                                        </Button>
                                                        <UncontrolledTooltip target="add-time-codes" placement="top">
                                                            Add Time Code
                                                        </UncontrolledTooltip>
                                                        <Modal isOpen={isOpenCreateCodeModal} toggle={()=>setIsOpenCreateCodeModal(o=>!o)}>
                                                            <ModalHeader className="bg-light border-bottom-0">
                                                                Create Time Code
                                                            </ModalHeader>
                                                            <ModalBody>
                                                            
                                                                <FormGroupInput
                                                                    id="new-code-title"
                                                                    label="Title"
                                                                    onChange={ e=>setCodeTitle(e.target.value) }
                                                                    value={codeTitle}
                                                                    invalid={ !!timeCodes?.find( type => type.title === typeTitle ) }
                                                                    feedback={ 'That title is already used'}
                                                                />
                                                            </ModalBody>
                                                            <ModalFooter className="d-flex bg-light border-top-0">
                                                                <CustomInput
                                                                    id="new-code-is-bankable"
                                                                    type="checkbox"
                                                                    style={{ zIndex: 0 }}
                                                                    checked={newCodeIsBankable}
                                                                    onChange={e => setNewCodeIsBankable(e.target.checked)}
                                                                    label="This time code is bankable"
                                                                />
                                                                <Button className="ml-auto" onClick={handleSubmitCode} color='primary' size='sm'>Submit</Button>
                                                            </ModalFooter>
                                                        </Modal>
                                                    </Col>
                                                </Row>
                                            </CardHeader>
                                            <BootstrapTable
                                                bootstrap4
                                                classes="table-dashboard table-striped table-sm fs--1 border-bottom border-200 mb-0 table-dashboard-th-nowrap"
                                                rowClasses="btn-reveal-trigger border-top border-200"
                                                headerClasses="bg-200 text-900 border-y border-200"
                                                wrapperClasses="mb-3"
                                                keyField="_id"
                                                data={timeCodes || []}
                                                columns={[
                                                    {
                                                        dataField: 'title', 
                                                        text: 'Title', 
                                                        formatter: (cell, row) => <h6>{cell}</h6>, // eslint-disable-line react/display-name
                                                        headerAlign: 'center',
                                                        headerClasses: 'border-0',
                                                        classes: 'border-0 py-2 align-middle',
                                                        sort: true
                                                    },
                                                    {
                                                        dataField: 'bankable',
                                                        text: 'Bankable',
                                                        formatter: (cell, row) => <FontAwesomeIcon icon={cell ? 'check' : 'times'} className={cell ? 'text-success' : 'text-secondary'} />, // eslint-disable-line react/display-name
                                                        headerAlign: 'center',
                                                        align: 'center',
                                                        headerClasses: 'border-0',
                                                        classes: 'border-0 py-2 align-middle',
                                                        sort: true
                                                    }
                                                ]}
                                                tabIndexCell
                                                defaultSortDirection="asc"
                                                defaultSorted={[{
                                                    dataField: 'title',
                                                    order: 'asc'
                                                    }]}
                                            />
                                        
                                    </>
                                )
                                : <CardBody><h5> Select Category to view codes</h5></CardBody>
                            }
                        </Card> 
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default TimesheetSettings