////// Packages //////////////////
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

////// Local Components //////////
import UsersTable from './components/UsersTable';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { getAdminOrgEmployees } from 'store/thunks/users';
import useAction from 'store/hooks/useAction';



const OrgUsers = () => {
    // const [pageNumber, setPageNumber] = useState(0);
    const pageNumber = 0;
    const { post } = useAction(getAdminOrgEmployees);
    const myOrgId = useSelector( state => state.app.login.organizationId)

    const numberPerPage = 100;
    // javascript here
    useEffect(() => {
        post(myOrgId, pageNumber, numberPerPage)
    }, []);
    
    return (
        // jsx here
        <UsersTable />
    )
}


export default OrgUsers;