////// Packages //////////////////
import React, { useEffect, useState, FC } from 'react';
import {
    Row,
    Col,
    Button,
    InputGroup,
    InputGroupAddon,
    Input,
    Label,
    FormGroup,
    Alert,
    Spinner
} from 'reactstrap';
import { useSelector } from 'react-redux';
import { isEqual } from 'lodash';
import dayjs from 'dayjs';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import moment from 'moment';
import DateTime from 'react-datetime';

////// Local Components //////////
import DetailRow from './DetailRow';

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';
import PhoneNumberInput from 'components/PhoneNumberInput';
import {IName} from 'store/types/models/id';

////// CommonJS Helpers //////////
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////
import { useAction } from 'store/hooks/useAction';

////// Thunks ////////////////////
import Loader from 'components/Loader';
import { setUserInfo } from 'store/thunks/users';

interface Props {
    userId: string;
    editMode: boolean;
    setEditMode: (mode: boolean) => void;
}

const PersonalInfo: FC<Props> = ({ userId, editMode, setEditMode }) => {
    const user = useSelector( state => state.id.users[userId] );
    const setInfo = useAction( setUserInfo );

    const {
        personId,
        // organizationId,
        internalId,
        startDate,
        // location,
        // authorized,
        // groupId,
        // permissions,
        // position,
        // manager,
        // manages,
        email,
        mailingAddress,
        phoneNumber
    } = user || {};

    const person = useSelector(state => state.id.people[personId], isEqual);

    const position = person?.position;
    const [newName, setNewName] = useState({} as IName);
    const [newID, setNewID] = useState('');
    const [newStartDate, setNewStartDate] = useState('');
    const [newPosition, setNewPosition] = useState('');
    const [newEmail, setNewEmail] = useState('');
    const [newPhone, setNewPhone] = useState('');
    const [newStreet, setNewStreet] = useState(['']);
    const [newCity, setNewCity] = useState('');
    const [newState, setNewState] = useState('');
    const [newZip, setNewZip] = useState('');

    const name = makeName(person?.name, { prefix: true, first: true, common: true, middle: true, last: true, title: true, reverse: false, coerceUpper: false });

    useEffect(() => { setNewName(person?.name || {}) }, [person?.name])
    useEffect(() => { setNewID(internalId || '') }, [internalId])
    useEffect(() => { setNewStartDate((startDate && dayjs(startDate).format('YYYY-MM-DD')) || '') }, [startDate])
    useEffect(() => { setNewPosition(position) }, [position])
    useEffect(() => { setNewEmail(email) }, [email])
    useEffect(() => { setNewStreet((mailingAddress?.street && mailingAddress?.street?.length > 0) ? mailingAddress.street : ['']) }, [mailingAddress?.street])
    useEffect(() => { setNewCity(mailingAddress?.city || '') }, [mailingAddress?.city])
    useEffect(() => { setNewState(mailingAddress?.state || '') }, [mailingAddress?.state])
    useEffect(() => { setNewZip(mailingAddress?.zipCode || '') }, [mailingAddress?.zipCode])
    useEffect(() => { setNewPhone(phoneNumber || '') }, [phoneNumber])

    const handleSubmit = async () => {
        const data = await setInfo.post(
                newName,
                newID,
                newStartDate,
                newPosition,
                newEmail,
                {
                    street: newStreet,
                    city: newCity,
                    state: newState,
                    zipCode: newZip
                },
                newPhone,
                userId
            )
        if (data?.success) {
            setEditMode(false);
        }
    }

    if (!person || !user) return <Loader />

    if (editMode) return (
        <>
            <Row>
                <Col lg className="col-xxl-5">
                    <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">Account Information</h6>
                    <FormGroupInput
                        value={newName?.prefix}
                        onChange={e => setNewName({ ...newName, prefix: e.target.value })}
                        label="Name Prefix"
                        id="name-prefix"
                        type="text"
                        name="prefix-name"
                    />
                    <FormGroupInput
                        value={newName?.first}
                        onChange={e => setNewName({ ...newName, first: e.target.value })}
                        label="First Name"
                        id="first-name"
                        type="text"
                        name="first-name"
                    />
                    <FormGroupInput
                        value={newName?.common}
                        onChange={e => setNewName({ ...newName, common: e.target.value })}
                        label="Common Name"
                        id="common-name"
                        type="text"
                        name="common-name"
                    />
                    <FormGroupInput
                        value={newName?.middle}
                        onChange={e => setNewName({ ...newName, middle: e.target.value })}
                        label="Middle Name"
                        id="middle-name"
                        type="text"
                        name="middle-name"
                    />
                    <FormGroupInput
                        value={newName?.last}
                        onChange={e => setNewName({ ...newName, last: e.target.value })}
                        label="Last Name"
                        id="last-name"
                        type="text"
                        name="last-name"
                    />
                    <FormGroupInput
                        value={newName?.title}
                        onChange={e => setNewName({ ...newName, title: e.target.value })}
                        label="Title"
                        id="name-title"
                        type="text"
                        name="suffix-name"
                    />
                    <FormGroupInput
                        value={newID}
                        onChange={e => setNewID(e.target.value)}
                        label="ID"
                        id="internal-id"
                        name="identifier"
                        type="text"
                    />
                    <FormGroup>
                        <Label htmlFor="user-start-date">Start Date</Label>
                        <DateTime
                            value={newStartDate}
                            onChange={(date) => setNewStartDate(moment(date).format("MM/DD/YYYY"))}
                            timeFormat={false}
                            dateFormat="MM/DD/YYYY"
                            inputProps={{
                                id: "user-start-date"
                            }}
                        />
                    </FormGroup>    
                    
                    <FormGroupInput
                        value={newPosition}
                        onChange={e => setNewPosition(e.target.value)}
                        label="Position"
                        id="position"
                        type="text"
                        name="position"
                    />
                </Col>
                <Col lg className="col-xxl-5 mt-4 mt-lg-0 offset-xxl-1">
                    <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">Contact Information</h6>
                    <FormGroupInput
                        value={newEmail}
                        onChange={e => setNewEmail(e.target.value)}
                        label="Email Address"
                        id="email"
                        type="email"
                    />
                    {/* put the street address here */}
                    {newStreet?.map((line, index, arr) => (
                        <FormGroup key={index}>
                            <Label htmlFor={`street-line-${index}`}>{`Street Line ${index + 1}`}</Label>
                            <InputGroup>
                                <Input
                                    value={line}
                                    onChange={e => {
                                        const arr = newStreet.slice();
                                        arr[index] = e.target.value;
                                        setNewStreet(arr);
                                    }}
                                    id={`street-line-${index}`}
                                    type="text"
                                    name="street-address"
                                />
                                {index === arr.length - 1 &&
                                    <InputGroupAddon addonType='append'>
                                        <Button
                                            color="primary"
                                            onClick={() => {
                                                const addedArray = newStreet.slice(0);
                                                addedArray.push('');
                                                setNewStreet(addedArray);
                                            }}
                                        >
                                            <FontAwesomeIcon icon="plus" />
                                        </Button>
                                        {arr.length > 1 &&
                                            <Button
                                                color="secondary"
                                                onClick={() => {
                                                    const removedArray = newStreet.slice(0);
                                                    if (removedArray.length !== 1) removedArray.pop();
                                                    setNewStreet(removedArray);
                                                }}
                                            >
                                                <FontAwesomeIcon icon="minus" />
                                            </Button>
                                        }
                                    </InputGroupAddon>
                                }
                            </InputGroup>
                        </FormGroup>
                    ))}
                    <FormGroupInput
                        value={newCity}
                        onChange={e => setNewCity(e.target.value)}
                        type="text"
                        label="City"
                        id="city"
                        name="city"
                    />
                    <FormGroupInput
                        value={newState}
                        onChange={e => setNewState(e.target.value)}
                        type="text"
                        label="State"
                        id="state"
                        name="state"
                    />
                    <FormGroupInput
                        value={newZip}
                        onChange={e => setNewZip(e.target.value)}
                        type="text"
                        label="Zip Code"
                        id="zip"
                        name="zipcode"
                    />
                    <PhoneNumberInput
                        value={newPhone}
                        onChange={e => setNewPhone(e.target.value)}
                        label="Phone Number"
                        id="phone-number"
                        type="tel"
                    />
                </Col>
            </Row>
            {setInfo.error && <Alert color="danger">{setInfo.error}</Alert>}
            <Row>
                <Col>
                    <Button color="primary" className="d-flex ml-auto" onClick={handleSubmit} >
                        Save Changes
                        {setInfo.loading && <Spinner color="light" size="sm" />}
                    </Button>
                </Col>
            </Row>
        </>
    )

    return (
        <Row>
            <Col lg className="col-xxl-5">
                <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">Account Information</h6>
                <DetailRow title="Name">{name}</DetailRow>
                <DetailRow title="ID">{internalId}</DetailRow>
                <DetailRow title="Start Date">{startDate && dayjs(startDate).format('MM/DD/YYYY')}</DetailRow>
                <DetailRow title="Position" isLastItem>
                    {position}
                </DetailRow>
            </Col>
            <Col lg className="col-xxl-5 mt-4 mt-lg-0 offset-xxl-1">
                <h6 className="font-weight-semi-bold ls mb-3 text-uppercase">Contact Information</h6>
                <DetailRow title="Email">
                    <a href={`mailto:${email}`}>{email}</a>
                </DetailRow>
                <DetailRow title="Mailing Address">
                    {mailingAddress &&
                        Array.isArray(mailingAddress.street) &&
                        <>
                            {mailingAddress.street.map((line, index) => <p className="mb-1" key={index}>{line}</p>)}
                            <p className="mb-1">{mailingAddress.city}{mailingAddress.state ? ', ' : ''}{mailingAddress.state} {mailingAddress.zipCode} </p>
                        </>
                    }
                </DetailRow>
                <DetailRow title="Phone number">
                    <a href={`tel:${phoneNumber}`}>{phoneNumber}</a>
                </DetailRow>
            </Col>
        </Row>
    );
};

export default PersonalInfo;