import React, {FC} from 'react';

import Avatar from "components/Avatar";
import { Link } from "react-router-dom";
import { Media } from "reactstrap";

import { MPerson } from 'store/types/models/id';
import Flex from 'components/Flex';
import makeName from 'helpers/makeName';

interface NameFormatterProps {
    _id: MPerson['_id'],
    avatar: MPerson['avatar'],
    name: MPerson['name']
}

const NameFormatter: FC<NameFormatterProps> = ({ _id, avatar, name }) => {
  const displayName = makeName(name, { first: true, common: true, last: true })
  return (
    <Link to={`/admin/user-details/${_id}`}>
      <Media tag={Flex} align="center">
        <Avatar src={avatar?.location} name={displayName} />
        <Media body className="ml-2">
          <h5 className="mb-0 fs--1">{displayName}</h5>
        </Media>
      </Media>
    </Link>
  );
};

export default NameFormatter