////// Packages //////////////////
import React, { useCallback, FC } from 'react';
import {
    CardHeader, 
    CardBody,
    Row, 
    Col, 
    Badge, 
    UncontrolledDropdown,
    DropdownMenu, 
    DropdownItem, 
    DropdownToggle,
    Media,
    Spinner
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';

////// Local Components //////////

////// Global Components /////////
import Avatar from 'components/Avatar';

////// CommonJS Helpers //////////
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////
import useUser from 'store/hooks/user/useUser';
import usePerson from 'store/hooks/person/usePerson';
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import Loader from 'components/Loader';
import {activateUser, deactivateUser} from 'store/thunks/users';

interface Props {
    userId: string;
}

const UserHeader: FC<Props> = ({userId}) => {
    const deactivate = useAction(deactivateUser);
    const activate = useAction(activateUser);

    const {value: user, ...userLoader} = useUser(userId);
    const {value: person, ...personLoader} = usePerson(user?.personId || '')


    const name = makeName(person?.name, {first: true, last: true});
    const email = user?.email;

    const handleDeactivateUser = useCallback(() => {
        if (person?._id) {
            deactivate.post(person._id)
        }    
    }, [deactivate.post, person?._id]);

    const handleActivateUser = useCallback(() => {
        if (person?._id) {
            activate.post(person._id)
        }    
    }, [activate.post, person?._id]);

    if (userLoader.loading || personLoader.loading || !person) return <Loader />

    return (
        <>
            <CardHeader className="pb-2">
                <Row>
                    <Col>
                        <Media>
                            {person?.avatar && 
                                <Media left middle className="mr-3">
                                    <Avatar size="4xl" src={[person.avatar.location]} name={makeName(person.name, {first: true, last: true})} />
                                </Media>
                            }
                            <Media body>
                                <h5 className="mb-0">
                                    <Link to={`/social/profile/${person?._id}`} className="text-900">{name}</Link> <a href={`mailto:${email}`} className="text-900 fs-0">({email})</a>
                                </h5>
                            </Media>
                        </Media>
                    
                    </Col>
                    <Col xs="auto" className="d-none d-sm-block">
                    <h6 className="text-uppercase text-600">
                        {person?.position}
                        <FontAwesomeIcon icon="user" className="ml-2" />
                    </h6>
                    </Col>
                </Row>
            </CardHeader>
            <CardBody className="pt-1">
                <Badge color={user?.active ? 'primary' : 'warning'} className="fs-0">
                    {user?.active ? 'Active' : 'Inactive'} 
                    {(deactivate.loading || activate.loading) && <Spinner color="light" size="sm" />}
                </Badge>
                <UncontrolledDropdown className="d-inline-block ml-4">
                    <DropdownToggle color="falcon-default" size="sm">
                        <FontAwesomeIcon icon="ellipsis-h" />
                    </DropdownToggle>
                    <DropdownMenu>
                        {user?.active ?
                            <DropdownItem onClick={handleDeactivateUser}>Deactivate</DropdownItem>
                            :
                            <DropdownItem onClick={handleActivateUser}>Activate</DropdownItem>
                        }   
                        <DropdownItem divider />
                        <DropdownItem className="text-danger">Delete user</DropdownItem>
                    </DropdownMenu>
                </UncontrolledDropdown>
            </CardBody>
        </>
    )
    
}

export default UserHeader;