import React, {FC} from 'react';
import { Row, Col } from 'reactstrap';

interface Props {
  title: string;
  isLastItem?: boolean;
  children: React.ReactNode;
  className?: string;
}

const DetailRow: FC<Props> = ({ title, isLastItem=false, children, className }) => (
    <Row className={className}>
      <Col xs={5} sm={4}>
        <p className={['font-weight-semi-bold', isLastItem ? 'mb-0' : 'mb-1'].join(' ')} >
          {title}
        </p>
      </Col>
      <Col>{children}</Col>
    </Row>
  );

 export default DetailRow;